      ],
      datguitest: { name: "123", nowsframe: 0 },
    };
  },
  components: {},

  mounted() {
    // Add a string controller.
    // const gui = new dat.GUI();
    // gui.add(this.datguitest, "name");
    // gui.add(this.datguitest, "nowsframe", 0, 200);
    this.Load();
    this.initLottie();
    this.initTimeLine();
    this.initScrollMagic();
    this.initP5();
    this.ismobile = this.mobile_detec();
    // console.log(screen.height);
    // console.log(screen.width);
    // console.log(window.innerHeight);
    // console.log(window.innerWidth);
  },
  computed: {},
  methods: {
    dattext() {},
    async runAnim(arr) {
      for (var i = arr[0]; i < arr[1]; i++) {
        await this.sleep(24);
        this.lottieD.goToAndStop(i, true);
      }
    },

    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    changeainme(index) {
      switch (index) {
        case 0:
          this.runAnim([99, 115]);

          break;
        case 1:
          this.showui = !this.showui;
          if (this.showui) this.runAnim([130, 149]);

          break;
        case 2:
          this.showfe = !this.showfe;
          if (this.showfe) this.runAnim([150, 168]);

          break;

        default:
          break;
      }
    },
    Load() {
      var delay = 6;
      if (this.ismobile) {
        delay = 8;
      } else {
        this.imgPreload();
      }
      gsap.from("#loadbar", delay, {
        scaleX: 0,
      });
      var vm = this;
      window.setTimeout(() => {
        vm.loadPage = true;
      }, delay * 1000);
    },
    imgPreload() {
      var img = new Image();
      img.src = require(`../assets/work/hand.png`);
      for (var i = 1; i < 5; i++) {
        img.src = require(`../assets/work/0${i}.png`);
      }
    },
    clickShowImage() {
      console.log(this.glists[this.clickindex]);
      if (this.glists[this.clickindex].url) {
        window.open(this.glists[this.clickindex].url);
        return;
      }
      if (!this.showImg) {
        this.showImg = true;
      } else {
        this.showImg = !this.showImg;
      }
    },
    clickGlist(e, i) {
      this.clickindex = i;
      this.desc = this.glists[i].desc;

      if (this.ismobile) {
        this.clickShowImage();
      }
      if (this.lastShowImg == "") {
        var nthd1 = e.currentTarget.parentElement.querySelector(".glist");
        nthd1.style.filter = "brightness(0.7)";
        nthd1.querySelector(".littletitle").style.color = "#a9adb1";
        this.lastShowImg = e.currentTarget;
      } else {
        this.lastShowImg.style.filter = "brightness(0.7)";
        this.lastShowImg.querySelector(".littletitle").style.color = "#a9adb1";
      }
      e.currentTarget.style.filter = "brightness(1)";
      e.currentTarget.querySelector(".littletitle").style.color = "#ffdd51";
      var path = this.glists[i].img;

      this.$refs.showimage.style.backgroundImage = `url(${require("../assets/work/" +
        path)})`;
      this.nowImg = path;
      this.lastShowImg = e.currentTarget;
    },
    initP5() {
      var vm = this;
      const script = function(p5) {
        // var speed = 4;
        // var posX = 0;
        var width = vm.$refs.bg.clientWidth;
        var height = vm.$refs.bg.clientHeight;
        var rendertree = [];
        var renderrgb = [];
        // eslint-disable-next-line no-unused-vars
        // NOTE: Set up is here
        p5.setup = () => {
          p5.createCanvas(width, height);
          for (let i = 0; i < Math.floor(p5.width / 25); i++) {
            rendertree.push(p5.random(30));
            // let r = p5.random(30);
          }
          for (let i = 0; i < Math.floor(p5.width / 25); i++) {
            renderrgb.push(p5.random(100, 255));
            // let r = p5.random(30);
          }
        };
        // NOTE: Draw is here
        p5.draw = () => {
          // const degree = p5.frameCount * 5;
          // const y = p5.sin(p5.radians(degree)) * 50;
          p5.background("#171717");
          drawRec();
          drawLine();
          // p5.push();
          // p5.noStroke();
          // p5.fill("#999999");
          // p5.translate(0, p5.height / 2);
          // p5.ellipse(posX, y, 10, 10);
          // p5.pop();
          // posX += speed;
          // if (posX > p5.width || posX < 0) {
          //   speed *= -1;
          // }
        };
        const drawLine = () => {
          var updateRec = p5.frameCount % 2 == 1 ? true : false;
          if (updateRec) {
            for (let i = 0; i < Math.floor(p5.width / 25); i++) {
              rendertree[i] = rendertree[i] + p5.random(-10, 10);
              // console.log(p5.noise(p5.mouseY));
              if (rendertree[i] < 0) {
                rendertree[i] = 0;
              } else if (rendertree[i] > 40) {
                rendertree[i] = 40;
              }
              // let r = p5.random(30);
            }
          }
          rendertree.forEach((value, i) => {
            p5.strokeWeight(4); // Thicker
            p5.stroke(renderrgb[i]);
            var dis = p5.mouseX - (i * 25 + 5);
            if (dis < 30 && dis > 5) {
              p5.push();
              p5.stroke("#ffdd51");
              p5.line(i * 25 + 10, height, i * 25 + 10, height - 40 - value);
              p5.pop();
            } else {
              p5.line(i * 25 + 10, height, i * 25 + 10, height - value);
            }
          });
        };
        // eslint-disable-next-line no-unused-vars
        const drawRec = () => {
          p5.push();
          p5.stroke("#292929");
          p5.strokeWeight(2);
          // var updateRec = p5.frameCount % 20 == 0 ? true : false;

          var x = Math.floor(width / 48);
          var y = Math.floor(height / 48);
          p5.noFill();

          for (var i = 0; i < x; i++) {
            // if (updateRec && p5.random(10) > 8) {
            //   var bright = p5.random(10);
            //   p5.fill(23 + bright, 23 + bright, 23 + bright);
            // }
            p5.rect(i * 48 + 1, height, 48, 48);
            for (var j = 1; j < y; j++) {
              // if (updateRec && p5.random(10) > 8) {
              //   var bright2 = p5.random(10);
              //   p5.fill(23 + bright2, 23 + bright2, 23 + bright2);
              // }
              p5.rect(i * 48 + 2, height - j * 48, 48, 48);
            }
          }
          p5.pop();
        };
      };
      // NOTE: Use p5 as an instance mode
      const P5 = require("p5");
      new P5(script, "bg");
    },
    splitPinMsg(tg) {
      return tg.split("");
    },
    clickCharacter() {},
    initScrollMagic() {
      var vm = this;
      this.MagicController = new ScrollMagic.Controller();
      var x = window.innerWidth;
      if (window.innerWidth > 1300) {
        x = 1300;
      }
      var peopleTween = gsap.timeline().to("#people", {
        y: window.innerHeight - window.innerHeight / 20,
        x: x / 4 - window.innerWidth / 20,
        scale: 1.2,
      });
      var subtitle = gsap.timeline().from(".subtitle", {
        opacity: 0,
        stagger: 0.2,
        scale: 0.8,
        y: 90,
      });
      var pinMsgTween = gsap.timeline().to(".msg", {
        opacity: 0,
        stagger: 0.2,
        scale: 0.8,
        y: 90,
      });
      var pinMsgTween2 = gsap.timeline().from(".msgs", {
        opacity: 0,
        stagger: 0.2,
      });
      var pinMsgTween3 = gsap.timeline().to(".msgs", {
        opacity: 0,
        stagger: 0.2,
        y: 90,
      });
      var pinMsgTween4 = gsap.timeline().from(".msgss", {
        opacity: 0,
        stagger: 0.2,
      });

      var circle = gsap.timeline().from(".outcircle", {
        y: -100,
      });

      // eslint-disable-next-line no-unused-vars
      var scene = new ScrollMagic.Scene({
        offset: 100, // start scene after scrolling for 100px
        duration: window.innerHeight / 2, // pin the element for 400px of scrolling
        triggerElement: this.$people, // starting scene, when reaching this element
        triggerHook: 0, // don't trigger until #pinned-trigger1 hits the top of the viewport
      })
        .setTween(peopleTween)
        .addTo(this.MagicController);
      // eslint-disable-next-line no-unused-vars
      var Pinscene = new ScrollMagic.Scene({
        offset: 100, // start scene after scrolling for 100px
        duration: window.innerHeight / 2, // pin the element for 400px of scrolling
        triggerHook: 0, // don't trigger until #pinned-trigger1 hits the top of the viewport
      })
        .setTween([pinMsgTween, pinMsgTween2])
        .addTo(this.MagicController);
      // eslint-disable-next-line no-unused-vars
      var Pinscene2 = new ScrollMagic.Scene({
        offset: window.innerHeight + 100, // start scene after scrolling for 100px
        duration: window.innerHeight / 2, // pin the element for 400px of scrolling
        triggerHook: 0, // don't trigger until #pinned-trigger1 hits the top of the viewport
      })
        .setTween([pinMsgTween3, pinMsgTween4])
        .addTo(this.MagicController);
      // eslint-disable-next-line no-unused-vars
      var scene2 = new ScrollMagic.Scene({
        offset: (window.innerHeight / 3) * 2, // start scene after scrolling for 100px
        duration: window.innerHeight / 3, // pin the element for 400px of scrolling
        // duration: (screen.height / 3) * 2 - 100, // pin the element for 400px of scrolling
        triggerElement: this.$secondpage, // starting scene, when reaching this element
        triggerHook: 0, // don't trigger until #pinned-trigger1 hits the top of the viewport
      })
        .setTween(subtitle)
        .addTo(this.MagicController);
      // eslint-disable-next-line no-unused-vars
      var scene3 = new ScrollMagic.Scene({
        offset: ((window.innerHeight * 2) / 3) * 2, // start scene after scrolling for 100px
        duration: (window.innerHeight * 2) / 3, // pin the element for 400px of scrolling
        // duration: (screen.height / 3) * 2 - 100, // pin the element for 400px of scrolling
        triggerElement: this.$thirdpage, // starting scene, when reaching this element
        triggerHook: 0, // don't trigger until #pinned-trigger1 hits the top of the viewport
      })
        .setTween(circle)
        .addTo(this.MagicController);

      scene.on("end", () => {
        vm.nowScene = [0, 16];
        vm.lottieD.playSegments(vm.nowScene, true);
      });
      scene.on("progress", function(event) {
        var frame = 43 + Math.floor(event.progress * 10);
        if (event.progress == 0) {
          vm.nowScene = [17, 32];
        }
        if (frame > 43 && frame < 53) {
          vm.lottieD.goToAndStop(frame, true);
        } else {
          vm.lottieD.playSegments(vm.nowScene, true);
        }
      });
    },
    playnowScene() {
      this.lottieD.playSegments([0, 16], true);
    },
    initTimeLine() {
      this.timeline = gsap.timeline();
      this.timeline2 = gsap.timeline();
      this.timeline3 = gsap.timeline();
      this.timeline3.from("#dots", {
        opacity: 0,
        delay: 0,
      });
      this.timeline3.pause();

      this.timeline2.from(".peopleContainer", {
        height: 0,
        delay: 2,
      });
      this.timeline2.pause();
      // this.timeline2.to(".containers", {
      //   delay: 1,
      //   x: -200,
      //   scale: 0.6,
      // });
      this.timeline.from(".t", 0.2, {
        delay: 0.3,
        opacity: 0,
        stagger: 0.2,
        scale: 0.8,
        y: 90,
      });
      this.timeline.pause();

      // this.timeline.to(".t", 0.8, {
      //   keyframes: [
      //     {
      //       scale: 1.3,
      //       ease: "Power1.in",
      //       stagger: 0.2
      //     },
      //     {
      //       scale: 1,
      //       ease: "Power2.in",
      //       stagger: 0.2
      //     }
      //   ]
      // });
      // this.timeline.from(".card", 1, {
      //   y: -500,
      //   x: -500,
      //   rotate: 300,
      //   ease: "circ.out",
      //   opacity: 0
      // });
      // this.timeline.pause();
    },
    initLottie() {
      this.lottieD = lottie.loadAnimation({
        container: document.querySelector(".peopleContainer"), // the dom element that will contain the animation
        renderer: "svg",
        loop: true,
        autoplay: true,
        animationData: require("../assets/bodymovin/data.json"), // the path to the animation json
      });
      var vm = this;
      this.lottieD.playSegments([17, 32], true);
      this.lottieD.addEventListener("loopComplete", () => {
        if (vm.hoverFlag) {
          vm.lottieD.pause();
        } else {
          vm.lottieD.play();
        }
      });
    },
    hoverCharacter(flag) {
      if (flag) {
        this.lottieD.playSegments([34, 56], true);
        this.hoverFlag = flag;
      } else {
        this.lottieD.playSegments(this.nowScene, true);
        this.hoverFlag = flag;
      }
    },
    again() {
      gsap.to("#hand", 0.2, {
        opacity: 0,
        x: 0,
        display: "none",
      });
      if (this.first) {
        this.timeline3.resume();
        this.timeline2.resume();
        this.timeline.resume();
        this.first = false;
      } else {
        return;
      }
      // this.lottieD.goToAndStop(this.testvar, true);
      // console.log(this.testvar);
      // this.testvar++;
    },
    hoverTitle() {},
    ligth_anim() {
      var vm = this;
      var width = document.documentElement.clientWidth / 2;
      var height = document.documentElement.clientHeight / 2;
      document.addEventListener("mousemove", function(e) {
        var tgW = e.clientX - width;
        var tgH = e.clientY - height;
        vm.$refs.light.style.transform = `translate(${-tgW / 5}px, ${-tgH /
          5}px)`;
      });
    },
  },
};
</script>

